











































































.language {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.language__icon {
  font-size: 1.2rem;
}

.language__inner {
  margin-left: 7px;
  .language__name {
    font-size: .8rem;
    color: #555;
  }
  .language__current {
    font-size: .7rem;
    color: #868686;
  }
}

.lang-item {
  display: inline-block;
  padding: 2px 10px;
  font-size: 1rem;
  margin: 5px;
}
