














































































.github {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.github__icon {
  font-size: 1.5rem;
}

.github__inner {
  margin-left: 7px;
  .github__name {
    font-size: .8rem;
    color: #555;
  }
  .github__info {
    display: flex;
    align-items: center;
    justify-content: center;
    > span {
      font-size: .7rem;
      color: #868686;
    }
    .github__message {
      font-size: .7rem;
    }
    .github__dot {
      border: 1px solid #777;
      margin: 0 10px;
      border-radius: 50%;
    }
  }
}
